import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
/**
 * 客户营销管理  
 * 
 * 下级需再分 module (客户联系 潜在客户管理 自选客户 客户申领 客户转让 客户分配 客户代管 客户开立)
 */
export const routes: Routes = [
     {
      path: 'cus-potential', loadChildren: './potential-customer-management/potential-custom.module#PotentialCusModule',//潜在客户管理
    },
    {
      path: 'group', loadChildren: './group/group.module#GroupModule',//自选客户群组
    },
    {
      path: 'cus-open', loadChildren: './custom-open/custom-open.module#CusOpenModule',//客户开立
    },
    {
      path: 'cus-transfer', loadChildren: './custom-transfer/custom-transfer.module#CusTransferModule',//客户转让
    },
    {
      path: 'cus-escrow', loadChildren: './custom-escrow/custom-escrow.module#CusEscrowModule',//客户代管
    },
    {
      path:'cus-apply',loadChildren: './custom-apply/custom-apply.module#CusApplyModule',//客户申领
    },
    {
      path:'cus-allot',loadChildren:'./custom-allot/custom-allot.module#CusAllotModule',//客户分配
    },
    {
      path:'cus-contact',loadChildren:'./custom-contact/custom-contact.module#CusContactModule',//客户联系
    },
    {
      path:'cus-manage',loadChildren:'./custom-manage/custom-manage.module#CusManageModule',//客户分配
    },
]

export const CusMarketingRouting: ModuleWithProviders = RouterModule.forChild(routes);
